import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { PrivacyPolicy } from './components/privacy-policy/privacy-policy.jsx';
import { NotFound } from './components/not-found/not-found.jsx';
import { Header } from './components/header/header.jsx';
import { HomeGames } from './components/home-games/home-games.jsx';
import { HomeApps } from './components/home-apps/home-apps.jsx';
import { HomeAbout } from './components/home-about/home-about.jsx';
import { Footer } from './components/footer/footer.jsx';
import { HomeContact } from './components/home-contact/home-contact.jsx';

function App() {
  return (
    <div className="app-container" > 
      <Header />
      <div className="main-container">
        <Switch>
          <Route exact path="/apps" component={ HomeApps } />
          <Route exact path="/games" component={ HomeGames } />
          <Route exact path="/about" component={ HomeAbout } />
          <Route exact path="/contact" component={ HomeContact } />
          <Route exact path="/privacy" component={ PrivacyPolicy } />
          <Route exact path={ [ "/", "/home" ] } component={ HomeGames } />
          <Route component={ NotFound } />
        </Switch>
      </div>
      <Footer />
    </div>
  );
}

export default App;
