import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import './header-nav.scss';

const links = [
    ['/apps', 'Apps'],
    ['/games', 'Games'],
    ['/about', 'About us'],
    ['/contact', 'Contact'],
    ['/privacy', 'Privacy']
];

export const HeaderNav = () => {
    const { pathname } = useLocation();
    return (
        <div className="header-nav-container">
            { links.map(([path, name]) => (
                <div key={ path } className={ `header-nav-container__item ${ pathname === path ? 'header-nav-container__item--active' : '' }` }>
                    <Link to={ path }>{ name }</Link>
                </div>
            )) }
        </div>
    )
};
