import React from 'react';
import './home-about.scss';
import { useInternalJsonFile } from '../../utils/hooks/useInternal';

export const HomeAbout = () => {
    const { content } = useInternalJsonFile('data/about.json', {})
    const getMessages = (message) => message.split("\n").map((i, key) => <p key={ key }>{ i }</p>)
    return (
        <div className="home-about-container">
            { !!content && !!content.length && content.map(({ title, message }) => (
                <div className="about-panel">
                    <h2>{ title }</h2>
                    { getMessages(message) }
                </div>
            )) }
        </div>
    )
};
